import headTop from "@/components/page-head/page-head.vue"
export default {
	data() {
		return {
			alarm_record:[]
		}
	},
	components:{
		headTop
	},
	methods: {
		
	},
	onShow() {
		uni.request({
			url:'/api/GreenCampus/alarm/allAlarmRecord',
			data:{
				pageNo : 1,
				pageSize: 10
			},
			success: (res) => {
				console.log(res);
				let len = res.data.detail.length;
				for(let i = 0; i<len; i++){
					this.alarm_record.push(res.data.detail[i]);
				}
			},
			fail: () => {
				console.log('error');
			}
		})
	}
}
